#filter {
  position: fixed;
  top: 10px;
  right: 60px;
  padding: 30px;
  background: #fff;
  z-index: 2;
  box-shadow: 0 10px 20px rgba(#000, .1);

  h2 {
    margin: 0;
  }

  input {
    width: 100%;
    border: 1px solid #ccc;
    border-radius: none;
    padding: 10px 15px;
  }
}